.skin-blue label {
  display: initial;
  max-width: 100%;
  margin-bottom: 5px;
  /* font-weight: 700 !important; */
}

.skin-blue .radio>label {
  position: relative;
  padding-left: 25px;
  margin-right: 0px;
  color: #666666;
}

.skin-blue .checkbox>label {
  position: relative;
  cursor: pointer;
  margin-right: 0px;
  padding-left: 25px;
}

.inline {
  display: inline-block;
  vertical-align: middle;
  padding-left: 5px;
  padding-right: 5px;
}

.tplLine span {
  line-height: 30px;
}

.input-small {
  margin-left: 5px;
  margin-right: 5px;
}

.chkText {
  display: inline;
  line-height: 30px;
  margin-left: 10px;
}

.el-select {
  width: auto;
}

.skin-blue input[readonly] {
  background-color: white !important;
  opacity: 1;
}

.el-radio__input {
  width: 14px;
}

.el-radio__label {
  display: none;
}
